<template>
  <div class="mid-content_map">
    <div id="midmap" class="mid-map"></div>
    <div class="float-info">
      <div class="float-info_time">{{currentTime}}</div>
      <div class="float-info_data">
        <div class="float-info_data-item">
          <div class="data-item_title">客户</div>
          <div class="data-item_num">
            198
            <span class="general-unit">/家</span>
          </div>
        </div>
        <div class="float-info_data-item">
          <div class="data-item_title">制衣厂</div>
          <div class="data-item_num">
            206
            <span class="general-unit">/家</span>
          </div>
        </div>
        <div class="float-info_data-item">
          <div class="data-item_title">面辅料商</div>
          <div class="data-item_num">
            445
            <span class="general-unit">/家</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
require("echarts/map/js/china.js");
import jsonp from "jsonp";
// require("echarts/map/js/province/hunan.js");

export default {
  name: "MidTop",
  data() {
    return {
      currentTime: null,
      factoryAddressObj: {},
      highCapacity: [],
      lowCapacity: [],
      middleCapacity: [],
      renderChartFlag: false
    };
  },
  props: ["mapData"],
  mounted() {
    this.renderChart();
    this.getTime();
  },
  watch: {
    mapData: {
      deep: true,
      handler(newValue, oldValue) {
        var factoryData = newValue.factoryData;
        this.$lodash.forIn(factoryData, (value, key) => {
          console.log(key);
          // var address = value[0].factoryAddress
          //   ? value[0].factoryAddress
          //   : "广州市越秀区307号富力新天地";
          // var url = `https://api.map.baidu.com/geocoder/v2/?address=${address}&output=json&ak=GZiLTIq1GSioocGC7rcCkK4enqyGidSb`;
          // jsonp(url, null, (err, data) => {
          //   if (err) {
          //     console.error(err.message);
          //   } else {
          //     var lng_lat = [
          //       data.result.location.lng,
          //       data.result.location.lat
          //     ];
          //     console.log(`${address}:`);
          //     console.log(lng_lat);
          //     this.factoryAddressObj[key] = lng_lat;
          //   }
          // });
          this.getLngLat(value[0].factoryAddress).then(val => {
            this.factoryAddressObj[key] = val;
          });
        });
        this.highCapacity = newValue.highCapacityData;
        this.middleCapacity = newValue.middleCapacityData;
        this.lowCapacity = newValue.lowCapacityData;
        this.renderChartFlag = true;
      }
    },
    "$store.state.currentComponent"(newValue) {
      if (newValue == "Dashboard" && this.renderChartFlag) this.renderChart();
    }
  },
  methods: {
    getTime() {
      this.currentTime = this.$moment().format("YYYY-MM-DD , dddd , HH:mm");
      var interval = 30 * 1000;
      setTimeout(() => {
        this.getTime();
      }, interval);
    },
    getLngLat(address = "广州市越秀区307号富力新天地") {
      var value = new Promise((resolve, reject) => {
        var url = `https://api.map.baidu.com/geocoder/v2/?address=${address}&output=json&ak=GZiLTIq1GSioocGC7rcCkK4enqyGidSb`;

        jsonp(url, null, (err, data) => {
          if (err) {
            console.error(err.message);
          } else {
            var lng_lat = [data.result.location.lng, data.result.location.lat];
            console.log(`${address}:`);
            console.log(lng_lat);
            resolve(lng_lat);
          }
        });
      });
      // console.log(value);
      return value;
    },
    renderChart() {
      var myChart = this.$echarts.init(document.getElementById("midmap"));
      var color = ["#ED2647", "#F1D832", "#17F2A8"];
      var series = [];
      [
        ["产能饱和", this.lowCapacity],
        ["产能预警", this.middleCapacity],
        ["产能充足", this.highCapacity]
      ].forEach((item, i) => {
        if (!this.$lodash.isEmpty(item[1])) {
          series.push({
            name: item[0],
            type: "effectScatter",
            coordinateSystem: "geo",
            zlevel: 2,
            rippleEffect: {
              brushType: "stroke"
            },
            label: {
              show: true,
              position: "right",
              formatter: function(params) {
                var val =
                  params.name.length > 5
                    ? params.name.slice(0, 5) + ".."
                    : params.name;
                return val;
              }
            },
            symbolSize: 8,
            itemStyle: {
              normal: {
                color: color[i]
              }
            },
            data: item[1].map(dataItem => {
              return {
                name: dataItem.name,
                value: this.factoryAddressObj[dataItem.name]
                  ? this.factoryAddressObj[dataItem.name]
                      .concat([dataItem.value])
                      .concat([
                        `${dataItem.name}工厂 订单总数/总产能 占比：${dataItem.value}%`
                      ])
                  : "-",
                tooltip: {
                  show: true,
                  trigger: "item",
                  formatter: function(params) {
                    return `<p>${params.value[params.value.length - 1]}</p>`;
                  }
                }
              };
            })
          });
        }
      });

      var option = {
        title: {
          text: "衣全球供应链远程监管云平台",
          left: "center",
          textStyle: {
            color: "#fff",
            fontSize: 32,
            fontWeight: 400
          }
        },
        tooltip: {
          trigger: "item"
        },
        legend: {
          orient: "vertical",
          top: "bottom",
          left: "left",
          data: ["产能饱和", "产能预警", "产能充足"],
          textStyle: {
            color: "#fff"
          },
          symbol: "arrow",
          selectedMode: "multiple"
        },
        geo: {
          map: "china",
          // boundingCoords: [
          //   // 定位左上角经纬度
          //   [100, 35],
          //   // 定位右下角经纬度
          //   [120, 20]
          // ],
          layoutCenter: ["50%", "55%"],
          layoutSize: "100%",
          zoom: 1.1,
          boundingCoords: null,
          selectedMode: true,
          label: {
            show: false,
            position: ["50%", "50%"],
            color: "#fff",
            emphasis: {
              show: true,
              color: "#fff"
            }
          },
          // roam: false,
          roam: true,
          itemStyle: {
            normal: {
              areaColor: "#0b1027",
              borderColor: "#1e2442"
            },
            emphasis: {
              areaColor: "#0f1739"
            }
          },
          regions: [
            {
              name: "广东",
              selected: true
            }
          ]
        },
        series: series
      };

      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option, true);
      this.renderChartFlag = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/sass/general.scss";
@font-face {
  font-family: "digital";
  src: url("../assets/digital-7.ttf");
}
.mid-content_map {
  position: relative;
  // width: vw(930);
  // height: vw(711);
  // background-color: #3493ca;
}
#midmap {
  width: vw(930);
  height: vw(711);
  //   width: 100%;
  //   height: 100%;
}
.float-info {
  position: absolute;
  top: vw(55);
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.float-info_time {
  font-size: vw(18);
  color: #7f90ab;
}
.float-info_data {
  display: flex;
  justify-content: space-between;
  margin-top: vw(10);
}
.float-info_data-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 0 vw(20);
}
.data-item_title {
  font-size: vw(14);
  color: #ffffff;
  font-weight: 400;
}
.data-item_num {
  font-family: "digital";
  font-size: vw(30);
  color: #20b2ef;
}
.general-unit {
  font-size: vw(14);
  font-weight: 400;
  color: #3a3e5e;
}
</style>