<template>
  <div class="mid-content_table general-flex">
    <div class="general-title">订单详情</div>
    <table class="order-table">
      <thead>
        <tr class="table-header">
          <th style="width:15%">生产订单</th>
          <th style="width:5%">品类</th>
          <th style="width:10%">件数</th>
          <th style="width:10%">客户</th>
          <th style="width:10%">交货地</th>
          <th style="width:15%">制衣厂</th>
          <th style="width:15%">产地</th>
          <th style="width:10%">交期</th>
          <th style="width:10%">派单</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="item in showData">
          <tr class="table-body">
            <td>{{item.productionOrder}}</td>
            <td>{{item.category}}</td>
            <td>{{item.quantity}}</td>
            <td>
              {{$lodash.truncate(item.client, {
              'length': 5,
              'omission': '..'
              })}}
            </td>
            <td>
              {{$lodash.truncate(item.clientAddress, {
              'length': 5,
              'omission': '..'
              })}}
            </td>
            <td>
              {{$lodash.truncate(item.factory, {
              'length': 8,
              'omission': '..'
              })}}
            </td>
            <td>
              {{$lodash.truncate(item.factoryAddress, {
              'length': 8,
              'omission': '..'
              })}}
            </td>
            <td>{{item.deliveryDate}}</td>
            <td
              :class="[item.orderStatus == '待派单' ? 'orderstatus-red' : 'orderstatus-white']"
            >{{item.orderStatus}}</td>
          </tr>
          <tr>
            <td colspan="9" class="table-line">
              <div
                :class="['table-line_progress', parseFloat(item.completion) > 50 ? 'progress-80' : 'progress-50']"
                :style="{width: item.completion}"
              ></div>
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "MidBottom",
  data() {
    return {
      showData: [],
      tempData: [],
      timeoutID: ""
    };
  },
  props: ["tableData"],
  watch: {
    tableData: {
      deep: true,
      handler(newValue, oldValue) {
        // console.log(`触发表格更新`);
        // console.log(newValue);
        this.tempData = newValue;
        this.get5Data();
      }
    }
  },
  methods: {
    get5Data() {
      clearInterval(this.timeoutID);
      if (!this.$lodash.isEmpty(this.tempData)) {
        let tempData = this.tempData;
        this.showData = this.$lodash.take(tempData, 4);
        console.log(this.showData);
        this.timeoutID = setInterval(() => {
          this.tempData =
            this.tempData.length > 4
              ? this.$lodash.drop(this.tempData)
              : this.tableData;
          let tempData = this.tempData;
          this.showData = this.$lodash.take(tempData, 4);
        }, 10000);
      } else {
        this.showData = [];
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/sass/general.scss";
.mid-content_table {
  width: vw(930);
  height: vw(289);
  padding: vw(14);
  background: rgba(4, 43, 91, 0.2);
  opacity: 0.8;
  border-radius: vw(7);
}
.order-table {
  margin-top: vw(16);
  width: 100%;
  //   height: 100%;
  border: none;
  border-spacing: 0;
  text-align: center;
}
.table-header {
  height: vw(44);
  background-color: #0f0c24;
  color: #3b54d3;
  font-size: vw(16);
  font-weight: 400;
}
.table-body {
  height: vw(38);
  color: #7f90ab;
  font-size: vw(16);
  font-weight: 400;
}
.table-line {
  height: vw(4);
}
.table-line_progress {
  height: 100%;
}
.progress-80 {
  background: #22e7a4;
}
.progress-50 {
  background: #22a4e7;
}
.orderstatus-red {
  color: red;
}
.orderstatus-white {
  color: white;
}

.general-title {
  padding-left: vw(13);
  border-left: vw(6) solid #3b54d3;
  font-size: vw(18);
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
}
.general-flex {
  display: flex;
  flex-direction: column;
  // justify-content: flex-start;
  // align-items: flex-start;
}
</style>