<template>
  <div class="left-content_data general-flex">
    <div class="general-title">平台主数据</div>
    <div class="left-content_data-details">
      <div class="detail-one">
        <img class="detail-one_img" src="../assets/productionorder.png" />
        <div class="detail-one_data">
          <div class="detail-one_data-title">在制生产订单</div>
          <div class="detail-one_data-num">
            <ICountUp :delay="delay" :endVal="dataOne" :options="options" @ready="onReady1" />
            <span class="general-unit">/笔</span>
          </div>
        </div>
      </div>
      <div class="detail-one general-margin-top">
        <img class="detail-one_img" src="../assets/orderquantity.png" />
        <div class="detail-one_data">
          <div class="detail-one_data-title">在制订单件数</div>
          <div class="detail-one_data-num" id="datatwo">
            <ICountUp :delay="delay" :endVal="dataTwo" :options="options" @ready="onReady2" />
            <span class="general-unit">/件</span>
          </div>
        </div>
      </div>
      <div class="detail-two">
        <div class="detail-two_item">
          <img class="detail-two_item-img" src="../assets/completeproductionorder.png" />
          <div class="detail-two_item-data">
            <div class="detail-two_item-title">完成生产订单</div>
            <div class="detail-two_item-num" id="datathree">
              <ICountUp :delay="delay" :endVal="dataThree" :options="options" @ready="onReady3" />
              <span class="general-unit">/笔</span>
            </div>
          </div>
        </div>
        <div class="detail-two_item">
          <img class="detail-two_item-img" src="../assets/numberofcompleted.png" />
          <div class="detail-two_item-data">
            <div class="detail-two_item-title">完成订单件数</div>
            <div class="detail-two_item-num" id="datafour">
              <ICountUp :delay="delay" :endVal="dataFour" :options="options" @ready="onReady4" />
              <span class="general-unit">/件</span>
            </div>
          </div>
        </div>
        <div class="detail-two_item">
          <img class="detail-two_item-img" src="../assets/purchaseorder.png" />
          <div class="detail-two_item-data">
            <div class="detail-two_item-title">在途采购订单</div>
            <div class="detail-two_item-num" id="datafive">
              <ICountUp :delay="delay" :endVal="dataFive" :options="options" @ready="onReady5" />
              <span class="general-unit">/笔</span>
            </div>
          </div>
        </div>
        <div class="detail-two_item">
          <img class="detail-two_item-img" src="../assets/completeorder.png" />
          <div class="detail-two_item-data">
            <div class="detail-two_item-title">完成采购订单</div>
            <div class="detail-two_item-num" id="datasix">
              <ICountUp :delay="delay" :endVal="dataSix" :options="options" @ready="onReady6" />
              <span class="general-unit">/笔</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ICountUp from "vue-countup-v2";

export default {
  data() {
    return {
      delay: 1000,
      options: {
        useEasing: true,
        useGrouping: true
      },
      dataOne: 36,
      dataOneFlag: -1,
      dataTwo: 555100,
      datatwoFlag: -22,
      dataThree: 52,
      dataThreeFlag: 1,
      dataFour: 758500,
      dataFourFlag: 22,
      dataFive: 149,
      dataFiveFlag: -1,
      dataSix: 201,
      dataSixFlag: 1
    };
  },
  components: {
    ICountUp
  },
  mounted() {
    this.data1calculation(50);
    this.data2calculation(1);
    this.data3calculation(50);
    this.data4calculation(1);
    this.data5calculation(10);
    this.data6calculation(10);
  },
  methods: {
    onReady1(instance, CountUp) {
      instance.update(this.dataOne);
    },
    onReady2(instance, CountUp) {
      instance.update(this.dataTwo);
    },
    onReady3(instance, CountUp) {
      instance.update(this.dataThree);
    },
    onReady4(instance, CountUp) {
      instance.update(this.dataFour);
    },
    onReady5(instance, CountUp) {
      instance.update(this.dataFive);
    },
    onReady6(instance, CountUp) {
      instance.update(this.dataSix);
    },
    data1calculation(minute = 1) {
      let interval = minute * 60 * 1000;
      setTimeout(() => {
        this.dataOne = this.dataOne + this.dataOneFlag;
        this.dataOneFlag = -this.dataOneFlag;
        let time = this.dataOneFlag > 0 ? 55 : 50;
        this.data1calculation(time);
      }, interval);
    },
    data2calculation(minute = 1) {
      let interval = minute * 60 * 1000;
      setTimeout(() => {
        this.dataTwo = this.dataTwo + this.datatwoFlag;
        this.datatwoFlag = this.datatwoFlag < 0 ? 24 : -22;
        this.data2calculation(1);
      }, interval);
    },
    data3calculation(minute = 1) {
      let interval = minute * 60 * 1000;
      setTimeout(() => {
        this.dataThree = this.dataThree + this.dataThreeFlag;
        this.data3calculation(50);
      }, interval);
    },
    data4calculation(minute = 1) {
      let interval = minute * 60 * 1000;
      setTimeout(() => {
        this.dataFour = this.dataFour + this.dataFourFlag;
        this.data4calculation(1);
      }, interval);
    },
    data5calculation(minute = 1) {
      let interval = minute * 60 * 1000;
      setTimeout(() => {
        this.dataFive = this.dataFive + this.dataFiveFlag;
        this.dataFiveFlag = -this.dataFiveFlag;
        let time = this.dataFiveFlag > 0 ? 12 : 10;
        this.data5calculation(time);
      }, interval);
    },
    data6calculation(minute = 1) {
      let interval = minute * 60 * 1000;
      setTimeout(() => {
        this.dataSix = this.dataSix + this.dataSixFlag;
        this.data6calculation(10);
      }, interval);
    }
  }
};
</script>

