<template>
  <a-layout>
    <a-layout-header style="background: #fff; padding: 0 0 0 24px">
      <ExcelImport />

      <a-button class="button-style" type="primary" @click="counter()">
        <a-icon type="plus" />新增数据
      </a-button>

      <a-button class="button-style" type="primary" @click="release()" :disabled="executable">
        <a-icon type="check" />确认发布
      </a-button>

      <a-popconfirm placement="bottomLeft" okText="清空" cancelText="取消" @confirm="clear">
        <template slot="title">
          <p>确定清空数据？清空后无法恢复</p>
        </template>
        <a-button class="button-style" type="danger">
          <a-icon type="delete" />清空数据
        </a-button>
      </a-popconfirm>
    </a-layout-header>
    <a-layout-content
      :style="{ margin: '24px 16px', padding: '24px', background: '#fff', minHeight: '280px' }"
    >
      <DataTable />
    </a-layout-content>
  </a-layout>
</template>

<script>
import DataTable from "@/components/DataTable.vue";
import ExcelImport from "@/components/ExcelImport.vue";
export default {
  data() {
    return {
      executable: false
    };
  },
  mounted() {},
  components: {
    DataTable,
    ExcelImport
  },
  watch: {
    "$store.state.executable"(newValue, oldValue) {
      this.executable = newValue;
    }
  },
  methods: {
    counter() {
      console.log(`计数器`);
      this.$store.dispatch("ENSURE_TRIGGER_ADD");
    },
    release() {
      console.log(`确认发布`);
      this.$store.dispatch("ENSURE_RELEASE");
      this.$message.success(`发布成功！`);
    },
    clear() {
      console.log(`清空数据`);
      this.$store.dispatch("ENSURE_RESET_DATA");
    }
  }
};
</script>

<style lang="scss" scoped>
</style>