<template>
  <a-layout id="components-layout-demo-custom-trigger">
    <a-layout-sider class="sider-class" collapsedWidth="0" collapsible v-model="collapsed">
      <div class="logo" />
      <a-menu theme="dark" mode="inline" :selectedKeys="selectedKey">
        <a-menu-item key="1" @click="jumpPage('Dashboard',1)">
          <a-icon type="heat-map" />
          <span>地图展示</span>
        </a-menu-item>
        <a-menu-item key="2" @click="jumpPage('DataMaintenance',2)">
          <a-icon type="database" />
          <span>数据维护</span>
        </a-menu-item>
      </a-menu>
    </a-layout-sider>
    <keep-alive>
      <component v-bind:is="currentComponent"></component>
    </keep-alive>
  </a-layout>
</template>
<script>
import Dashboard from "./Dashboard.vue";
import DataMaintenance from "./DataMaintenance.vue";
export default {
  data() {
    return {
      collapsed: true,
      selectedKey: ["1"],
      currentComponent: "Dashboard"
    };
  },
  components: {
    Dashboard,
    DataMaintenance
  },
  methods: {
    jumpPage(router, key) {
      this.currentComponent = router;
      this.selectedKey = [`${key}`];
      this.$store.dispatch("ENSURE_CURRENT_COMPONENT", router);
    }
  }
};
</script>
