<template>
  <div class="basediv">
    <div class="left-content">
      <LeftTop />
      <LeftMid />
      <LeftBottom />
    </div>
    <div class="mid-content">
      <MidTop :mapData="mapData" />
      <MidBottom :tableData="tableData" />
    </div>
    <div class="right-content">
      <RightTop />
      <RightBottom />
    </div>
  </div>
</template>

<script>
import LeftTop from "@/components/LeftTop.vue";
import LeftMid from "@/components/LeftMid.vue";
import LeftBottom from "@/components/LeftBottom.vue";

import MidTop from "@/components/MidTop.vue";
import MidBottom from "@/components/MidBottom.vue";

import RightTop from "@/components/RightTop.vue";
import RightBottom from "@/components/RightBottom.vue";

export default {
  data() {
    return {
      mapData: {},
      tableData: []
    };
  },
  components: {
    LeftTop,
    LeftMid,
    LeftBottom,
    MidTop,
    MidBottom,
    RightTop,
    RightBottom
  },
  watch: {
    "$store.state.mainData": {
      deep: true,
      handler(newValue, oldValue) {
        var tempClientObj = {};
        var tempFactoryObj = {};
        var highCapacity = [];
        var lowCapacity = [];
        var middleCapacity = [];
        if (Array.isArray(newValue) && newValue.length > 0) {
          newValue.forEach((item, index) => {
            var clientName = item.client;
            var factoryName = item.factory;
            if (clientName) {
              var clientExist = this.$lodash.has(tempClientObj, clientName);
              if (clientExist) {
                tempClientObj[clientName].push(item);
              } else {
                tempClientObj[clientName] = [item];
              }
            }
            if (factoryName) {
              var factoryExist = this.$lodash.has(tempFactoryObj, factoryName);
              if (factoryExist) {
                tempFactoryObj[factoryName].push(item);
              } else {
                tempFactoryObj[factoryName] = [item];
              }
            }
          });
          this.$lodash.forIn(tempFactoryObj, (value, key) => {
            var factoryName = key;
            var totalCapacity = value[0].capacity;
            var totalPiece = 0;
            value.forEach((item, index) => {
              totalPiece = totalPiece + item.quantity;
            });
            var capacity = this.$lodash.round(
              (totalPiece / totalCapacity) * 100,
              2
            );
            if (capacity >= 90) {
              lowCapacity.push({ name: factoryName, value: capacity });
            } else if (capacity >= 80) {
              middleCapacity.push({ name: factoryName, value: capacity });
            } else {
              highCapacity.push({ name: factoryName, value: capacity });
            }
          });
        }
        this.$set(this.mapData, "clientData", tempClientObj);
        this.$set(this.mapData, "factoryData", tempFactoryObj);
        this.$set(this.mapData, "lowCapacityData", lowCapacity);
        this.$set(this.mapData, "middleCapacityData", middleCapacity);
        this.$set(this.mapData, "highCapacityData", highCapacity);
        console.log(this.mapData);
        this.tableData = newValue;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/sass/general.scss";
@font-face {
  font-family: "digital";
  src: url("../assets/digital-7.ttf");
}

.basediv {
  background-image: url("../assets/app_background.png");
  width: vw(1920);
  height: vw(1079.808);
  display: flex;
  justify-content: center;
}
.left-content {
  padding: vw(20) vw(10) vw(20) vw(10);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.mid-content {
  padding: vw(20) vw(8);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.right-content {
  padding: vw(20) vw(10) vw(20) vw(10);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
</style>