export function SET_INIT_DATA(state, data) {
    state.initData = data;
}

export function SET_INIT_DATA_2(state, data) {
    state.initData2 = data;
}

export function SET_COUNT(state, data) {
    state.count = state.count + data;
}

export function SET_COUNT_2(state, data) {
    state.count2 = state.count2 + data;
}

export function SET_TRIGGER_ADD(state) {
    state.triggerAdd++;
}

export function SET_TRIGGER_ADD_2(state) {
    state.triggerAdd2++;
}

export function SET_RELEASE(state) {
    state.mainData = state.initData;
}

export function SET_RELEASE_2(state) {
    state.mainData2 = state.initData2;
}

export function SET_RESET_DATA(state) {
    state.triggerReset++;
    state.count = 0;
    state.initData = [];
    state.mainData = [];
}

export function SET_RESET_DATA_2(state) {
    state.triggerReset2++;
    state.count2 = 0;
    state.initData2 = [];
    state.mainData2 = [];
}

export function SET_CURRENT_COMPONENT(state, data) {
    state.currentComponent = data;
}