<template>
  <div class="left-content_gauge general-flex">
    <div class="general-title">交期达成率</div>
    <div id="midgauge"></div>
  </div>
</template>

<script>
export default {
  name: "LeftMid",
  mounted() {
    this.renderGauge();
  },
  methods: {
    renderGauge() {
      var myGauge = this.$echarts.init(document.getElementById("midgauge"));
      var option = {
        tooltip: {
          formatter: "{a} : {c}%"
        },
        series: [
          {
            name: "交期达成率",
            type: "gauge",
            center: ["30%", "90%"],
            startAngle: 180,
            endAngle: 0,
            splitNumber: 4,
            radius: "175%",
            splitLine: {
              show: true,
              length: 30,
              lineStyle: {
                color: "#1ee8f7"
              }
            },
            pointer: {
              length: "45%",
              width: 5
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: [[0.25, "#e94f4f"], [0.75, "#1c91e8"], [1, "#17f1a7"]],
                width: 20
              }
            },
            axisTick: {
              show: true,
              splitNumber: 17,
              length: 20,
              lineStyle: {
                width: 1,
                type: "solid",
                color: "rgba(0, 0, 0, 1)"
              }
            },
            detail: {
              show: true,
              formatter: "{value}%",
              offsetCenter: ["160%", "-8%"],
              fontSize: 30
            },
            data: [
              {
                value: 50
              }
            ]
          }
        ]
      };
      option.series[0].data[0].value =
        (Math.random() * (100 - 95) + 95).toFixed(2) - 0;
      myGauge.setOption(option, true);
      setInterval(function() {
        option.series[0].data[0].value =
          (Math.random() * (100 - 95) + 95).toFixed(2) - 0;
        myGauge.setOption(option, true);
      }, 60000);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/sass/general.scss";
.left-content_gauge {
  width: vw(455);
  height: vw(201);
  padding: vw(14);
  background: rgba(4, 43, 91, 0.2);
  opacity: 0.8;
  border-radius: vw(7);
}
#midgauge {
  width: 100%;
  height: 100%;
}

.general-title {
  padding-left: vw(13);
  border-left: vw(6) solid #3b54d3;
  font-size: vw(18);
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
}
.general-flex {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  // align-items: flex-start;
}
</style>