<template>
  <a-table :columns="columns" :dataSource="data" bordered>
    <template
      v-for="col in ['productionOrder', 'category', 'code', 'quantity', 'client', 'clientAddress', 'factory', 'factoryAddress', 'deliveryDate', 'completion', 'orderStatus', 'capacity']"
      :slot="col"
      slot-scope="text, record, index"
    >
      <div :key="col">
        <a-input
          v-if="record.editable"
          style="margin: -5px 0"
          :value="text"
          @change="e => handleChange(e.target.value, record.key, col)"
        />
        <template v-else>{{text}}</template>
      </div>
    </template>
    <template slot="operation" slot-scope="text, record, index">
      <div class="editable-row-operations">
        <span v-if="record.editable">
          <a @click="() => save(record.key)">保存</a>
          <a-popconfirm title="确认取消?" @confirm="() => cancel(record.key)">
            <a>取消</a>
          </a-popconfirm>
        </span>
        <span v-else>
          <a @click="() => edit(record.key)">编辑</a>
        </span>
      </div>
      <a-popconfirm v-if="data.length" title="确认删除这条数据?" @confirm="() => onDelete(record.key)">
        <a href="javascript:;">删除</a>
      </a-popconfirm>
    </template>
  </a-table>
</template>
<script>
const columns = [
  {
    title: "生产订单",
    dataIndex: "productionOrder",
    scopedSlots: { customRender: "productionOrder" }
  },
  {
    title: "品类",
    dataIndex: "category",
    scopedSlots: { customRender: "category" }
  },
  {
    title: "CODE",
    dataIndex: "code",
    scopedSlots: { customRender: "code" }
  },
  {
    title: "件数",
    dataIndex: "quantity",
    scopedSlots: { customRender: "quantity" }
  },
  {
    title: "客户",
    dataIndex: "client",
    scopedSlots: { customRender: "client" }
  },
  {
    title: "交货地址",
    dataIndex: "clientAddress",
    scopedSlots: { customRender: "clientAddress" }
  },
  {
    title: "厂商",
    dataIndex: "factory",
    scopedSlots: { customRender: "factory" }
  },
  {
    title: "生产地址",
    dataIndex: "factoryAddress",
    scopedSlots: { customRender: "factoryAddress" }
  },
  {
    title: "交期",
    dataIndex: "deliveryDate",
    scopedSlots: { customRender: "deliveryDate" }
  },
  {
    title: "完成率",
    dataIndex: "completion",
    scopedSlots: { customRender: "completion" }
  },
  {
    title: "派单情况",
    dataIndex: "orderStatus",
    scopedSlots: { customRender: "orderStatus" }
  },
  {
    title: "产能",
    dataIndex: "capacity",
    scopedSlots: { customRender: "capacity" }
  },
  {
    title: "操作",
    dataIndex: "operation",
    scopedSlots: { customRender: "operation" }
  }
];

export default {
  name: "DataTable",
  data() {
    return {
      data: this.$store.state.initData,
      cacheData: [],
      columns
    };
  },
  computed: {},
  watch: {
    "$store.state.triggerReset"(newValue) {
      this.data = [];
    },
    "$store.state.initData"(newValue) {
      if (!this.$lodash.isEqual(this.data, newValue)) {
        console.log(`更新表格`);
        this.data = this.data.concat(newValue);
        this.$store.dispatch("ENSURE_INIT_DATA", this.data);
      }
    },
    "$store.state.triggerAdd"(newValue) {
      console.log(newValue);
      console.log(this.$store.state.count);
      this.handleAdd(this.$store.state.count);
    }
  },
  methods: {
    handleAdd(i) {
      console.log(`执行`);
      console.log(i);
      const { data } = this;
      const newData = {
        key: i + 1,
        productionOrder: `默认值请修改`,
        category: `默认值请修改`,
        code: `默认值请修改`,
        quantity: `默认值请修改`,
        client: `默认值请修改`,
        clientAddress: `默认值请修改`,
        factory: `默认值请修改`,
        factoryAddress: `默认值请修改`,
        deliveryDate: `默认值请修改`,
        completion: `80%`,
        orderStatus: `待派单`,
        capacity: `默认值请修改`
      };
      this.data = [...data, newData];
      console.log(this.data);
      this.$store.dispatch("ENSURE_INIT_DATA", this.data);
      this.$store.dispatch("ENSURE_COUNT", 1);
    },
    onDelete(key) {
      const dataSource = [...this.data];
      this.data = dataSource.filter(item => item.key !== key);
      this.$store.dispatch("ENSURE_INIT_DATA", this.data);
    },
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    edit(key) {
      this.cacheData = JSON.parse(JSON.stringify(this.data));
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target.editable = true;
        this.data = newData;
      }
    },
    save(key) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        delete target.editable;
        this.data = newData;
        this.$store.dispatch("ENSURE_INIT_DATA", this.data);
      }
    },
    cancel(key) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        delete target.editable;
        this.data = this.cacheData;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.editable-row-operations a {
  margin-right: 8px;
}
</style>