<template>
  <a-upload
    action="https://localhost/testupload"
    accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
    :showUploadList="false"
    @change="handleChange"
    :disabled="executable"
  >
    <a-button type="primary">
      <a-icon v-if="executable" type="loading" />
      <a-icon v-else type="upload" />上传Excel文件
    </a-button>
  </a-upload>
</template>

<script>
import XLSX from "xlsx";

export default {
  name: "ExcelImport",
  data() {
    return {
      processedHeader: [],
      processedHeader2: [],
      correspondence: {
        生产订单: "productionOrder",
        品类: "category",
        // CODE: "code",
        件数: "quantity",
        客户: "client",
        交货地: "clientAddress",
        制衣厂: "factory",
        产地: "factoryAddress",
        交期: "deliveryDate",
        派单情况: "orderStatus",
        产能: "capacity"
      },
      correspondence2: {
        类别: "classification",
        占比: "proportion",
        "总产能/月(件)": "totalCapacity",
        "当前可用产能/本月": "currentCapacity"
      },
      executable: false
    };
  },
  methods: {
    handleChange(info) {
      this.executable = true;
      this.$store.state.executable = true;
      if (info.file.status !== "uploading") {
        console.log(info.file, info.fileList);
      }
      if (info.file.status === "done") {
        this.$message.success(`${info.file.name} file uploaded successfully`);
      } else if (info.file.status === "error") {
        this.$message.success(`解析 ${info.file.name} 文件成功`);
        if (FileReader.prototype.readAsBinaryString === undefined) {
          FileReader.prototype.readAsBinaryString = function(fileData) {
            var binary = "";
            var pt = this;
            var reader = new FileReader();
            reader.onload = function(e) {
              var bytes = new Uint8Array(reader.result);
              var length = bytes.byteLength;
              for (var i = 0; i < length; i++) {
                binary += String.fromCharCode(bytes[i]);
              }
              //pt.result  - readonly so assign content to another property
              pt.content = binary;
              pt.onload(); // thanks to @Denis comment
            };
            reader.readAsArrayBuffer(fileData);
          };
        }
        var fileReader = new FileReader();
        fileReader.onload = ev => {
          var data = !ev ? fileReader.content : ev.target.result;
          try {
            // const data = ev.target.result;
            const workbook = XLSX.read(data, {
              type: "binary"
            });
            console.log(workbook);
            const wsname = workbook.SheetNames[0]; //取第一张表
            const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]); //生成json表格内容
            console.log(ws);
            let outputs = [];
            ws.forEach((item, index) => {
              let sheetData = {};
              this.$lodash.forIn(ws[index], (value, key) => {
                if (index == 0) {
                  this.processedHeader.push(key.trim());
                }
                sheetData[this.correspondence[key.trim()]] =
                  key.trim() == "交期"
                    ? this.$moment(new Date(1900, 0, value - 1)).format(
                        "YYYY/MM/DD"
                      )
                    : value;
              });
              sheetData = this.$lodash.assignIn(sheetData, {
                key: index + this.$store.state.count + 1,
                completion: `${this.$lodash.random(0, 100)}%`,
                code: "xxxx"
              });
              outputs.push(sheetData);
            });
            console.log(outputs);
            this.$store.dispatch("ENSURE_INIT_DATA", outputs);
            this.$store.dispatch("ENSURE_COUNT", outputs.length);
            this.executable = false;
            this.$store.state.executable = false;
          } catch (e) {
            this.$message.error(`解析 ${info.file.name} 错误`);
            return false;
          }
        };
        fileReader.readAsBinaryString(info.file.originFileObj);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
</style>